import React from 'react';
import './css/general.css';
import { MdShoppingCart } from 'react-icons/md';

class CartWidget extends React.Component {
   render(){
      return(
        <div>
          <div className="shop-ui cart--desktop coloured-ui grad--6 shadowed"><MdShoppingCart size="40px" /></div>
          <div className="shop-ui cart--desktop--counter">{this.props.count}</div>
        </div>
      );
   }
}

class DeliveryOptions extends React.Component {

  constructor (props) {
    super(props);
    this.state = {
      goal: 4000,
      prices: [380, 0, 150],
      isReached: false,
    };
    this.goalStatus = this.goalStatus.bind(this);
  }

  componentWillMount(){
    if (this.props.total >= this.state.goal){
      this.state.prices = [0,0,0];
      this.state.isReached = true;
    }
  }

  showPrice(price){
    if (price==0){
      return(
        <div>
          Бесплатно
        </div>
      )
    }
    else{
      return(
        <div>
          {price} ₽
        </div>
      )
    }
  }

  goalStatus(){
    if (this.state.isReached==true){
      return(
        <div>
          Опа, ты достиг цели!
        </div>
      );
    }
    else{
      return(
        <div>
          Вам осталось набрать всего-лишь {this.state.goal - this.props.total} ₽ до бесплатной доставки!
        </div>
      );
    }
  }

   render(){
      return(
        <div className="delivery-m">
          <div className="goal">
            {this.goalStatus()}
          </div>
          <div className="shop-ui divider m-10"/>
          <div className="column">
            <div className="header">
              Доставка курьером
            </div>
            <div>{this.showPrice(this.state.prices[0])}</div>
          </div>
          <div className="column">
            <div className="header">
              Самовывоз
            </div>
            <div>{this.showPrice(this.state.prices[1])}</div>
          </div>
          <div className="column">
            <div className="header">
              Почтой
            </div>
            <div>{this.showPrice(this.state.prices[2])}</div>
          </div>
          <div className="shop-ui divider m-10"/>
        </div>
      );
   }
}

class ShowCart extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      elems: null,
    };
  }

  renderEveryItem(arr){
    let itemsArr = [];
    for (let elem of arr) {
      itemsArr.push(<RenderedItem elem={elem} deleteFunc={this.props.deleteFunc}/>)
    }
    this.setState({elems : itemsArr});
  }

  deleteItem(id){
    console.log(id);
  }

  componentDidMount(){
    document.title = "Главная";
    this.renderEveryItem(this.props.cart)
  }

   render(){
      return(
        <div>
          {this.state.elems}
        </div>
      );
   }
}

class RenderedItem extends React.Component {

  deleteItem(id){
    this.props.deleteFunc(id);
  }
  render(){
    return(
      <div>
        <div className="shop-ui cart-item">
          <img className="shop-ui cart-image" src='https://s7d2.scene7.com/is/image/homedepotcanada/p_1000417094.jpg?$plpProduct$'/>
          <div className="shop-ui cart-container">
            <div className="shop-ui cart item title">
              {this.props.elem.name}
            </div>
            <div className="price">
              {this.props.elem.price} ₽
            </div>
            <button onClick={this.deleteItem(this.props.elem.id)}>Удалить</button>
          </div>
        </div>
        <div className="shop-ui divider"/>
      </div>
    )
  }
}

export {
  CartWidget,
  ShowCart,
  DeliveryOptions
}
