import React from 'react';
import './css/general.css';
import Swal from 'sweetalert2'
import ReactTooltip from 'react-tooltip'

import withReactContent from 'sweetalert2-react-content'
const sweetAlert = withReactContent(Swal);


class ItemCard extends React.Component {
   render(){
      return(
        <div className="shop-ui item card">
          <img className="image" src='https://s7d2.scene7.com/is/image/homedepotcanada/p_1000417094.jpg?$plpProduct$'/>
          <div className="title">Тупо светильник</div>
          <div className="description">Даже добавить нечего</div>
          <div className="price">1500₽</div>
          <button className="shop-ui button">В корзину!</button>
        </div>
      );
   }
}

class ItemCardTransparent extends React.Component {

  constructor(props) {
        super(props);
        this.addToCart = this.addToCart.bind(this);
    }

  addToCart(){
    this.props.addToCart({id:1,price:1500,name:"Тупо светильник",image:"https://s7d2.scene7.com/is/image/homedepotcanada/p_1000417094.jpg?$plpProduct$"});

    const toast = Swal.mixin({
      toast: true,
      position: 'center-end',
      showConfirmButton: false,
      timer: 10000,
    });
    toast({
      type: 'success',
      title: 'Товар добавлен в корзину!',
    })
  }

   render(){
      return(
        <div className="shop-ui item--transparent card">
          <div className="image-wrapper" onClick={this.addToCart}>
            <div className="block--service fade"/>
            <img className="image" src='https://s7d2.scene7.com/is/image/homedepotcanada/p_1000417094.jpg?$plpProduct$'/>
          </div>
          <div className="title">Тупо светильник</div>
          <div className="bottom-align">
            <div className="price">1 500 ₽</div><div className="price--past">3 000 ₽</div>
            <div className="bonus" data-tip="Вы получите данные бонусы сразу же <br/> после покупки!">+150 бонусов</div>
            <ReactTooltip effect="solid" place="right" className="shop-ui tooltip" multiline='true'/>
          </div>
        </div>
      );
   }
}

const ItemsGroup = ({children}) => (
  <div className="items--wrapp">
      {children}
  </div>
);


export {
  ItemCard,
  ItemCardTransparent,
  ItemsGroup
}
