import React, { Component } from 'react';
import { NavbarMainPage, WelcomeWrapp, ContentWrapp, NormalHeader, CategoriesGroup } from '../components/Components';
import { FeaturedCardsBlock, CardsHolder, FeaturedCards, FeaturedCard, FeaturedCardMini, CategoryCard } from '../components/Cards';
import {ItemCard, ItemCardTransparent, ItemsGroup} from '../components/Items';
import {CartWidget} from '../components/Cart';
import {saveToLocal} from '../components/StateHandler'


class App extends Component {

  constructor (props) {
    super(props);
    this.state = {
      cart: [],
      test: 0,
    };
    this.addToCart = this.addToCart.bind(this);
    this.getLocalCart = this.getLocalCart.bind(this);
  }

  getLocalCart() {
      var cartvar = localStorage.getItem('cart');
      cartvar = JSON.parse(cartvar);
      if (cartvar !== null){
        this.setState((state) => {
        return {cart: cartvar}
        });
      }
  }

  addToCart(item) {
    this.setState((state) => {
    // Important: read `state` instead of `this.state` when updating.
    return {cart: state.cart.concat(item)}
    });
    saveToLocal('cart', this.state.cart);
  };

  componentDidMount(){
    document.title = "Главная";
    this.getLocalCart();
  }

  componentDidUpdate() {
    saveToLocal('cart', this.state.cart);
  }

  render() {
    return (
      <React.Fragment>
        <WelcomeWrapp>
          <CartWidget count={this.state.cart.length}/>
          <NavbarMainPage cartCount={this.state.cart.length}/>

          <FeaturedCardsBlock>
            <FeaturedCard title="Подарки к Новому году" description="Интересные идеи для подарков"/>
            <CardsHolder>
              <FeaturedCardMini gradient="1" title="Подарки к Новому году" description="Интересные идеи для подарков"/>
              <FeaturedCardMini gradient="4" title="Подарки к Новому году" description="Интересные идеи для подарков"/>
            </CardsHolder>

            <CardsHolder>
              <FeaturedCardMini gradient="5" title="Подарки к Новому году" description="Интересные идеи для подарков"/>
              <FeaturedCardMini gradient="3" title="Подарки к Новому году" description="Интересные идеи для подарков"/>
            </CardsHolder>
          </FeaturedCardsBlock>

        </WelcomeWrapp>

        <ContentWrapp>
        <NormalHeader>Популярные категории</NormalHeader>
          <CategoriesGroup>
            <CategoryCard/>
            <CategoryCard/>
            <CategoryCard/>
            <CategoryCard/>
            <CategoryCard/>
            <CategoryCard/>
          </CategoriesGroup>
        </ContentWrapp>

        <ContentWrapp>
        <NormalHeader>Популярные товары</NormalHeader>
          <ItemsGroup>
            <ItemCardTransparent addToCart={this.addToCart}/>
            <ItemCardTransparent addToCart={this.addToCart}/>
            <ItemCardTransparent addToCart={this.addToCart}/>
            <ItemCardTransparent addToCart={this.addToCart}/>
            <ItemCardTransparent addToCart={this.addToCart}/>
            <ItemCardTransparent addToCart={this.addToCart}/>
          </ItemsGroup>
        </ContentWrapp>


      </React.Fragment>
    );
  }
}

export default App;
