import React, { Component } from 'react';
import { ShowCart, DeliveryOptions } from '../components/Cart'
import {saveToLocal} from '../components/StateHandler'
import { Navbar, WelcomeWrapp } from '../components/Components';

export default class Cart extends Component {

  constructor (props) {
    super(props);
    this.state = {
      test: 0
    };
    this.addToCart = this.addToCart.bind(this);
    this.getLocalCart = this.getLocalCart.bind(this);
    this.ShowTotalPrice = this.showTotalPrice.bind(this);
    this.deleteFunc = this.deleteFunc.bind(this);
  }

   getLocalCart() {
       var cartvar = localStorage.getItem('cart');
       cartvar = JSON.parse(cartvar);
       if (cartvar !== null){
         this.setState((state) => {
         return {cart: cartvar}
         });
       }
   }

  addToCart(item) {
    this.setState((state) => {
    // Important: read `state` instead of `this.state` when updating.
    return {cart: state.cart.concat(item)}
    });
    saveToLocal('cart', this.state.cart);
  };

  deleteFunc(id){
    var result = this.state.cart.find(e => e.id === id);
    console.log(result);
  }

  componentWillMount(){
    this.getLocalCart();
  }

  componentDidMount(){
    document.title = "Главная";
  }

  componentDidUpdate() {
    saveToLocal('cart', this.state.cart);
  }

  waitForStateAndShowCart(){
    return(<ShowCart cart={this.state.cart} deleteFunc={this.deleteFunc}/>);
  }

  waitForStateAndShowNavbar(){
    return(
      <Navbar
        name={this.state.name}
        cartCount={this.state.cart.length}
        display="mini"
      />
    )
  }

  showTotalPrice(arr){
    var total = 0;
    for (let elem of arr) {
      total = total + elem.price;
    }
    this.state.total = total;
    return total;
  }

  renderEveryItem(arr){
    let itemsArr = [];
    for (let elem of arr) {
      itemsArr.push(this.renderItem(elem))
    }
    this.setState({elems : itemsArr});
  }

  render() {
    return (
      <div className="shop-ui content m-100 flex-j w-1200">
        <div className="shop-ui cart container">
        {
          this.state && this.state.cart &&
          this.waitForStateAndShowNavbar()
        }
        {
          this.state && this.state.cart &&
					this.waitForStateAndShowCart()
				}
        </div>

        <div className="shop-ui cart info">
          <DeliveryOptions total={this.state && this.state.cart && this.showTotalPrice(this.state.cart)}/>
          <div className="total">Итого {this.state.total} ₽</div>
        </div>

      </div>
    );
  }
}
