import React from 'react';
import './css/admin.css';


class AdminNavbar extends React.Component {
   render(){
      return(
          <header className="shop-ui-admin navbar">
            <div className="logo">React-store</div>
          </header>
      );
   }
}

class StatsCard extends React.Component {
   render(){
      return(
          <div className="shop-ui-admin card">
            <h2>{this.props.value}</h2>
            <p>{this.props.description}</p>
          </div>
      );
   }
}


const MainContent = ({children}) => (
  <div className="shop-ui-admin content">
    {children}
  </div>
);

const CardBig = ({children}) => (
  <div className="shop-ui-admin card-big">
    {children}
  </div>
);

const CardsGroup = ({children}) => (
  <div className="shop-ui-admin cards-group">
    {children}
  </div>
);

export {
    AdminNavbar,
    StatsCard,
    MainContent,
    CardsGroup,
    CardBig
}
