import React from 'react';
import './css/general.css';


const FeaturedCardsBlock = ({children}) => (
  <div className="shop-ui featured-block">
    {children}
  </div>
);

const CardsHolder = ({children}) => (
  <div className="shop-ui cards-holder">
    {children}
  </div>
);

class FeaturedCard extends React.Component {
   render(){
      return(
        <div className={`shop-ui featured card coloured-ui grad--${ this.props.gradient }`}>
          <h2>{this.props.title}</h2>
          <p className="description" >{this.props.description}</p>
          <button className="shop-ui button abs end-par">Перейти</button>
        </div>
      );
   }
}

class FeaturedCardMini extends React.Component {
   render(){
      return(
        <a className={`shop-ui featured card mini coloured-ui grad--${ this.props.gradient }`}>
          <h2>{this.props.title}</h2>
          <p className="description" >{this.props.description}</p>
        </a>
      );
   }
}

class CategoryCard extends React.Component {
   render(){
      return(
        <a className="shop-ui cat--card" id="category">
          <div className="image-wrapper"></div>
          <p className="description" >Продукты</p>
        </a>
      );
   }
}

export {
    FeaturedCardsBlock,
    CardsHolder,
    FeaturedCard,
    FeaturedCardMini,
    CategoryCard
}
