import React from 'react';
import './css/general.css';
import svg_1 from './images/1.svg';

const WelcomeWrapp = ({children}) => (
  <div className="shop-ui nav-main padd">
    <div className="content--f">
      <div>{children}</div>
    </div>
  </div>
);

const ContentWrapp = ({children}) => (
  <div className="shop-ui content">
      {children}
  </div>
);


class Navbar extends React.Component {

  ShowLogoOrNot(){
    if (this.props.display != 'mini'){
      return(<div className="logo">React-store</div>);
    }
  }

   render(){
      return(
          <header className={`shop-ui navbar display--${ this.props.display }`}>
            <div className="centered">
              {this.ShowLogoOrNot()}
              <div className="nav-right">
                <a href="/">Главная</a>
                <a href="../cart">Корзина <span className="cart--counter">{this.props.cartCount}</span></a>
                <a href="../profile">Профиль</a>
              </div>
            </div>
          </header>
      );
   }
}

class NavbarMainPage extends Navbar {
   render(){
      return(
          <div>
            <Navbar cartCount={this.props.cartCount}/>
            <div id='show--navbar' className="d--none">
              <Navbar display="mini"
                      cartCount={this.props.cartCount}
              />
            </div>
          </div>
      );
   }
}

const NormalHeader = ({children}) => (
  <h2 className="shop-ui header--normal">
      {children}
  </h2>
);

const CategoriesGroup = ({children}) => (
    <div className="items--wrapp">
      {children}
    </div>
);

export {
    Navbar,
    WelcomeWrapp,
    ContentWrapp,
    NormalHeader,
    CategoriesGroup,
    NavbarMainPage,
}
